@import (reference) 'common/color.less';
.fillstu-news-box{width:773px;margin:0 auto;overflow:hidden;color:@black}
.sun-icon{
  width:22px;
  height:17px;
  display:inline-block;
  background:url("../../bundle/img/icon.png") no-repeat;
  background-position:-608px -129px;
}
.mt90{margin-top:90px;}
.mt60{margin-top:60px;}
.mb60{margin-bottom:60px;}
input{
  border:1px solid @head-line;
  display:inline-block;
  height:25px;
  vertical-align:middle;
  border-radius:2px;
  padding-left:3px;
  color:@head-line;
  font-size:14px;
}
.name-input{width:70px;}
.news-box li{float:left;width:33.33%;}
.email-box li{float:left;width:49%;}
.mb68{margin-bottom:68px;}
.phone-input{width:120px;}
.email-input{width:220px;}
.error-box{
  width:120px;
  height:25px;
  border-radius:4px;
  font-size:12px;
  text-align:center;
  border:1px solid @red;
  color:@red;
  line-height:25px;
  margin-top:5px;
  margin-left:30px;
  background:@tip-bg;
}
.clear-box{
  display: inline-block;
  height: 30px;
  line-height: 30px;
  width: 90px;
  text-align: center;
  color: #ffffff;
  cursor: pointer;
  background-color: #bababa;
  border-radius: 2px;
}